import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'

Vue.use(Vuex)

export default new Vuex.Store({
  state: function () {
    return {
      playMethod: 0, // 012
      playerShow: false, // 
      playlist: [], // 
      playlistShow: false, // 
      // 
      song: {
        id: null, // id
        name: '', // 
        album: {
          mid: '' // id
        },
        lyric: {
          lyric: '',
          lyricArr: [],
          timeArr: []
        }
      },
      songIndex: null, // 
      duration: 0, // 
      playingTime: 0, // 
      currentLyricIndex: 0, // 
      playState: false // 
    }
  },
  getters: {
    getAlbumCover: function (state) {
      if (state.song.id) {
        var albummid = ''
        if (state.song.album) {
          albummid = state.song.album.mid
        } else {
          albummid = state.song.albummid
        }
        return 'https://y.gtimg.cn/music/photo_new/T002R500x500M000' + albummid + '.jpg'
      }
    },
    getPlayMethod: function (state) {
      var icon = ''
      var text = ''
      if (state.playMethod === 0) {
        icon = '&#xe61f;'
        text = ''
      } else if (state.playMethod === 1) {
        icon = '&#xe61e;'
        text = ''
      } else if (state.playMethod === 2) {
        icon = '&#xe612;'
        text = ''
      }
      return { icon, text }
    }
  },
  mutations,
  actions
})



